	module.exports = {"data":{"markdownRemark":{"html":"<p>The <strong>Dwarves</strong> were made by Aulë, whom they themselves call Mahal, meaning “maker.” Aulë was unwilling to wait for the coming of the Children of Ilúvatar, for he was impatient and desired to have someone to teach his lore and crafts. Therefore, he made the first Seven Fathers of the Dwarves in secret in a hall under the mountains of Middle-earth.</p>\n<p>It was, however, not within Aulë's power to create life. After being reprimanded by Ilúvatar and realizing his error, Aulë offered his creations to his father to do with as he would, including their destruction. Even as the offer was made, Ilúvatar accepted and gave the Dwarves a life of their own. So when Aulë picked up a great hammer to smite the Seven Fathers and destroy his presumptuous creations, they shrank back in fear and begged for mercy.</p>\n<p>Ilúvatar was however not willing to suffer that the Dwarves should come before the Firstborn (Elves), and he decreed that the Seven Fathers should sleep underground and should not come forth until the Firstborn had awakened.</p>\n<h2>Abilities</h2>\n<p>Due to the Dwarves high strength and very high constitution they make great warriors.</p>\n<table class=\"table\">\n    <thead>\n        <tr>\n            <th>Strength</th>\n            <th>Intelligence</th>\n            <th>Will</th>\n            <th>Dexterity</th>\n            <th>Constitution</th>\n            <th>Learning Ability</th>\n        </tr>\n    </thead>\n    <tbody>\n        <tr>\n            <td>High</td>\n            <td>Normal</td>\n            <td>Low</td>\n            <td>Very Low</td>\n            <td>Very High</td>\n            <td>Low</td>\n        </tr>\n    </tbody>\n</table>","frontmatter":{"title":"Dwarves","raceid":4}}},"pathContext":{"path":"/races/dwarves"}}

